@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #ffffff;
    --color-light: rgba(255, 255, 255, 0.6);
    --transition: all 400ms ease;

    --container--width-lf: 75%;
    --container--width-md: 85%;
    --container--width-ms: 90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    color: var(--color-white);
    line-height: 1.7;
    background: #1f1f38;
    /* background: repeating-linear-gradient(90deg, transparent 0 5px, rgb(14 181 181 / 25%) 5px 10px), repeating-linear-gradient(50deg, #2431913b 0 5px, rgb(0 67 255 / 23%) 5px 10px); */
}

.container{
    width: var(--container--width-lf);
    margin: 0 auto;
}

h1, h2, h3, h4, h5, h6{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    /* height: 100vh; */
    height: auto;
}

section > h2,
section > h5{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.textLight{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btnPrimary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* =========== MEDIA QUERIES (MEDIUM DEVICES) ============ */

@media screen and (max-width: 1024px){
    .container{
        width: var(--container--width-md);
    }

    section{
        /* height: auto; */
        margin-top: 6rem;
    }
}

/* =========== MEDIA QUERIES (SMALL DEVICES) ============ */

@media screen and (max-width: 768px){
    .container{
        width: var(--container--width-ms);
    }

    section > h2{
        margin-top: 2rem;
    }
}

.LinkActive{
    background: #1f1f38 !important;
    color: #ffffff !important;
}